.heading__title {
  font-size: 18px;
  line-height: 26px;
  margin: 0 auto 38px;
  color: #000000;
  max-width: 270px;

  @media (min-width: $tablet-width) {
    max-width: 520px;
    font-size: 21px;
  }
}

.heading__title--s30 {
  @media (min-width: $tablet-width) {
    max-width: 435px;
    font-size: 30px;
  }
}

.heading__subtitle {
  margin: 0;

  br {
    display: none;
  }

  @media (min-width: $tablet-width) {
    br {
      display: block;
    }
  }
}
